import './App.css';

//Libs
import React, { PureComponent } from 'react';
import classnames from 'classnames';

//Components
import Article from './components/Article';
import NavList from './components/NavList';
import Popup from './components/Popup';

// Stubs
import navlists from './stubs/navlists';
import menu from './stubs/menu';
import favs from './stubs/favs';
import games from './stubs/games';
import articles from './stubs/articles';

class App extends PureComponent {
  state = {
    popupOpen: false,
  }

  togglePopup = (e) => {
    this.setState({ popupOpen: !this.state.popupOpen });
  }

  render() {
    return (
      <div className="App">
        <div className="container">
          <header>
            <div className="Logo">role.play</div>
            <div className="Menu">
              { menu.map((e, i) => 
                <a className={classnames({'active': i === 0})} key={ i } href={'/'}>{ e }</a>)
              }
            </div>
            <div className="Menu__mobile" onClick={this.togglePopup}>
              <div className="Menu__burger"></div>
            </div>
          </header>
          <nav>
            {
              navlists.map((e, i) => (
                <NavList key={i} {...e} />
              ))
            }
          </nav>
          <main>
            <h1 className="NavList__title">Лучшие посты недели</h1>
            {
              articles.map((e, i) => <Article key={ i } {...e} />)
            }
          </main>
          <aside>
            <p className="AsideTitle">Мои игры</p>
            {
              games.map((e, i) => (
                <NavList key={i} {...e} />
              ))
            }
            {
              favs.map((e, i) => (
                <NavList key={i} {...e} />
              ))
            }
          </aside>
          <footer>footer</footer>
        </div>
        <Popup showPopup={ this.state.popupOpen } togglePopup={ this.togglePopup }>
          { menu.map((e, i) => (
            <div key={ i } > 
              <a className={classnames({'active': i === 0})} href={'/'}>{ e }</a>
            </div>
            ))
          }
        </Popup>
      </div>
    );
  }
}

export default App;



