import './index.css';

import React, { PureComponent } from 'react';
import moment from 'moment';

class Article extends PureComponent {
  render() {
    const {
      user,
      timestamp,
      parent,
      body,
      counter
    } = this.props;

    const date = moment(timestamp).format('DD/MM/YYYY');
    const time = moment(timestamp).format('HH:mm');

    return (
      <div className="Article">
        <div className="Article__header">
          <span className="Article__user">
            { user.name }
          </span>
          <span className="Article__date">
            { date }
          </span>
          <span>
            { time }
          </span>
        </div>

        {
          parent && (
            <div className="Article__header Article__user">
              { parent.name }
            </div>            
          )
        }

        { 
          body.map((e, i) => <p key={ i } className="Article__body">{ e }</p>)
        }

        <div className="Article__star">
          { counter }
        </div>
      </div>
    );
  }
}

export default Article;


