import './index.css';

import React, { PureComponent } from 'react';
import classnames from 'classnames';

class NavList extends PureComponent {
  render() {
    const {
      className,
      title,
      items,
      link,
    } = this.props;

    return (
      <div className={classnames('NavList', {[className]:className})}>
        { title && (
          <div className="NavList__title">{ title }</div>
        ) }

        { items && items.length > 0 &&
          items.map((e, i) => <div className="NavList__item" key={i}>{ e }</div>
        )}

        { link && (
          <a className="NavList__link" href={ link.href }>{ link.text }</a>
        ) }
      </div>
    );
  }
}

export default NavList;


